:root {
  --animation: linear 300ms;
  --main-font: "Quicksand", sans-serif;
  --main-text-color: #efeee0;
  --page-background-color: #1d2123;
  --dark-grey-color: #0f1212;
  --accent-color: #7234a1;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--main-font);
  color: var(--main-text-color);
  letter-spacing: .03em;
}

li {
  align-items: center;
  list-style-type: none;
}

ol, ul {
  margin: 0;
  padding-top: 0;
  padding-left: 0;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.page-container {
  background-color: var(--page-background-color);
}

.container {
  width: 480px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 100%;
  }
}

.section {
  padding-top: 0;
  padding-bottom: 0;
}

.header-container {
  box-shadow: 0 5px 4px -4px var(--accent-color);
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  display: flex;
}

.logo {
  transition: text-shadow var(--animation);
}

.logo:hover, .logo:focus {
  cursor: pointer;
  text-shadow: 0px 3px 3px var(--accent-color);
}

.logo-text {
  color: var(--main-text-color);
  font-family: Parisienne, cursive;
  font-size: 30px;
  font-weight: 700;
}

.logo-wrapper:hover .logo {
  cursor: pointer;
  transform: rotate(-50deg);
}

.search-label {
  width: 100%;
  height: 50px;
}

.search-input-wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  width: 100%;
  height: 50px;
  background-color: var(--dark-grey-color);
  color: var(--main-text-color);
  transition: border var(--animation);
  border: 1px solid #2a2a2a;
  border-radius: 5px;
  padding: 5px 50px;
}

.search-input:hover, .search-input:focus {
  border: 1px var(--accent-color) solid;
}

.search-icon {
  height: 20px;
  width: 20px;
  fill: var(--main-text-color);
  transition: all var(--animation);
  position: relative;
  left: 35px;
}

.main-section__wrapper {
  display: flex;
}

.side-bar {
  flex-direction: column;
  gap: 25px;
  display: flex;
}

.main-section-container {
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  display: flex;
}

.side-bar__list {
  height: 380px;
  width: 50px;
  background-color: var(--dark-grey-color);
  transition: all var(--animation);
  border: 1px solid #000;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  padding: 10px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.side-bar__list:has(.side-bar__list-element:hover) {
  width: 120px;
}

.side-bar__list:has(.side-bar__list-element__link:focus) {
