}

.side-bar__list-element__link:focus > svg > use {
  fill: var(--accent-color);
}

.side-bar__list-element__icon {
  width: 25px;
  height: 25px;
  fill: var(--main-text-color);
}

.side-bar__list-element__icon:hover, .side-bar__list-element__icon:focus {
  cursor: pointer;
}

.profile__list-element {
  margin-top: 30px;
}

.side-bar__text {
  letter-spacing: 1px;
